.filter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 37px;
}

.filter select {
    padding: 3px 8px;
    border: none;
    border-radius: 8px;
    box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px, rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
}